* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f9f9f9;
  padding: 0 20px;
  font-family: Roboto, Calibri, Arial, sans-serif;
  line-height: 1.7;
}

.top-bar {
  z-index: 1000;
  background-color: #fff;
  align-items: center;
  gap: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 5px #0000001a;
}

.logo-container {
  flex-shrink: 0;
}

.logo {
  max-width: 80px;
  height: auto;
  display: block;
}

.tabs {
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: center;
  gap: 5px;
  display: flex;
}

header {
  color: #fff;
  text-align: center;
  background-image: url("image.fe65411b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 40px;
  position: relative;
}

header:before {
  content: "";
  z-index: 1;
  background-color: #00000026;
  border-radius: 10px;
  position: absolute;
  inset: 0;
}

.header-content, .tagline {
  z-index: 2;
  position: relative;
}

.header-content {
  justify-content: center;
  margin-bottom: 15px;
}

.banner-text {
  text-align: center;
}

.banner-text h1 {
  text-shadow: 1px 1px 3px #0006;
  margin: 0;
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.2;
}

.tagline {
  opacity: .95;
  text-shadow: 1px 1px 2px #00000059;
  margin-top: 0;
  font-size: 1.5rem;
  font-weight: 300;
}

.tab-button {
  cursor: pointer;
  color: #555;
  background: #e0e0e0;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 500;
  transition: background .3s, color .3s, transform .2s, box-shadow .2s, border-color .3s;
  box-shadow: 0 1px 3px #0000000d;
}

.tab-button.active {
  color: #fff;
  background: #2575fc;
  border-color: #2575fc;
  transform: translateY(-2px);
  box-shadow: 0 2px 5px #2575fc4d;
}

.tab-button:not(.active):hover {
  color: #333;
  background: #d0d0d0;
  border-color: #bbb;
  transform: translateY(-1px)scale(1.02);
  box-shadow: 0 2px 8px #0000001a;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.tab-content {
  opacity: 0;
  background: #fff;
  border-radius: 10px;
  padding: 25px;
  display: none;
  box-shadow: 0 4px 8px #00000014;
}

.tab-content h2, .tab-content h3 {
  color: #333;
  margin-bottom: 15px;
}

.tab-content h3 {
  margin-top: 30px;
  font-size: 1.3rem;
  font-weight: 600;
}

.tab-content.active {
  animation: .5s ease-out forwards fadeIn;
  display: block;
}

.section-intro-text, .section-outro-text {
  color: #555;
  margin-bottom: 20px;
  font-size: 1rem;
  line-height: 1.7;
}

.section-outro-text {
  margin-top: 20px;
}

.content-card {
  background: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 8px;
  align-items: flex-start;
  gap: 15px;
  padding: 20px;
  transition: transform .3s ease-out, box-shadow .3s ease-out, border-color .3s ease-out;
  display: flex;
  box-shadow: 0 2px 5px #0000000f;
}

.content-card:hover {
  border-color: #6a11cb;
  transform: translateY(-5px);
  box-shadow: 0 5px 12px #0000001a;
}

.content-card i {
  color: #4a90e2;
  text-align: center;
  flex-shrink: 0;
  width: 30px;
  margin-top: 5px;
  font-size: 1.8rem;
  transition: color .3s ease-out;
}

.product-item.content-card {
  align-items: center;
}

.product-item.content-card i {
  margin-top: 0;
}

.content-card:hover i {
  color: #6a11cb;
}

.content-card .text-content {
  flex-grow: 1;
}

.content-card .text-content h4 {
  color: #333;
  margin-bottom: 8px;
  font-size: 1.1rem;
  font-weight: 600;
}

.content-card .text-content p {
  color: #555;
  font-size: .95rem;
  line-height: 1.6;
}

.card-grid-container {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

.intro-card i {
  color: #6a11cb;
}

.intro-card:hover i {
  color: #2575fc;
}

footer {
  color: #ecf0f1;
  text-align: center;
  background: #2c3e50;
  border-top: 5px solid #2575fc;
  margin-top: 40px;
  padding: 40px 20px;
}

.footer-content-wrapper {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
}

.footer-contact h4, .footer-social h4 {
  color: #fff;
  margin-bottom: 15px;
  font-size: 1.2rem;
  font-weight: 500;
}

.footer-contact p {
  margin-bottom: 10px;
  font-size: 1rem;
}

.footer-contact a {
  color: #3498db;
  text-decoration: none;
  transition: color .3s;
}

.footer-contact a:hover {
  color: #5dade2;
  text-decoration: underline;
}

.footer-contact i {
  color: #3498db;
  margin-right: 10px;
}

.whatsapp-button {
  background-color: #25d366;
  border-radius: 25px;
  align-items: center;
  padding: 12px 20px;
  font-weight: 500;
  text-decoration: none;
  transition: background-color .3s, transform .2s, box-shadow .2s;
  display: inline-flex;
  box-shadow: 0 2px 4px #0000001a;
  color: #fff !important;
}

.whatsapp-button:hover {
  background-color: #1dae52;
  text-decoration: none;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #00000026;
  color: #fff !important;
}

.whatsapp-button i.fa-whatsapp {
  margin-right: 10px;
  font-size: 1.3rem;
  color: #fff !important;
}

.footer-copyright {
  color: #bdc3c7;
  border-top: 1px solid #34495e;
  padding-top: 20px;
  font-size: .9rem;
}

@media (width <= 768px) {
  body {
    padding: 0 15px;
  }

  .top-bar {
    gap: 15px;
  }

  .logo {
    max-width: 70px;
  }

  header {
    margin-top: 15px;
    padding: 30px 20px;
  }

  .banner-text h1 {
    font-size: 2.5rem;
  }

  .tagline {
    font-size: 1.25rem;
  }

  .tab-button {
    padding: 8px 15px;
    font-size: .9rem;
  }

  .card-grid-container {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 15px;
  }

  .content-card {
    padding: 15px;
  }

  .content-card i {
    margin-top: 3px;
    font-size: 1.6rem;
  }

  .product-item.content-card i {
    margin-top: 0;
  }

  .footer-content-wrapper {
    gap: 20px;
  }

  .footer-contact h4, .footer-social h4 {
    font-size: 1.1rem;
  }

  .whatsapp-button {
    padding: 10px 15px;
    font-size: .9rem;
  }

  .whatsapp-button i.fa-whatsapp {
    font-size: 1.1rem;
  }
}

@media (width <= 480px) {
  body {
    padding: 0 10px;
  }

  .top-bar {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .logo {
    max-width: 60px;
  }

  .tabs {
    justify-content: center;
    width: 100%;
  }

  header {
    margin-top: 10px;
    padding: 25px 15px;
  }

  .banner-text h1 {
    font-size: 2rem;
    line-height: 1.3;
  }

  .tagline {
    font-size: 1.1rem;
  }

  .tab-button {
    flex-grow: 1;
    min-width: auto;
    padding: 8px 10px;
    font-size: .85rem;
  }

  .tab-content h2 {
    font-size: 1.5rem;
  }

  .tab-content h3 {
    font-size: 1.15rem;
  }

  .card-grid-container {
    grid-template-columns: 1fr;
  }

  .content-card .text-content h4 {
    font-size: 1rem;
  }

  .content-card .text-content p {
    font-size: .9rem;
  }

  .footer-content-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .footer-contact, .footer-social, .footer-contact h4, .footer-social h4 {
    text-align: center;
  }
}

@media (width >= 768px) {
  .footer-content-wrapper {
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
  }

  .footer-contact, .footer-social {
    text-align: left;
    flex: 1;
    max-width: 400px;
  }

  .footer-contact h4, .footer-social h4 {
    text-align: left;
  }
}
